@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
html,body{
    padding: 0;
    margin: 0;
    height: 100%;
}
body{
    font-family: Microsoft JhengHei,'Roboto', sans-serif;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
#app{
  margin: 0 auto;
}
ul{
    list-style: none;
    padding: 0;
}
.main{
    display: flex;
    flex-direction: row;
    align-items: center;
    @media (max-width:768px) {
      flex-direction: column;
    }
}
.video1{
    width: 50%;
    height: auto;
    outline: none;
    @media (max-width:768px) {
      width: 100%;
    }
}
.music-wrap{
    padding: 0 10px;
    max-width: 480px;
}
.music-list{
    height: 320px;
    overflow-y: auto;
    li{
      font-size: 24px;
      margin-bottom: 7px;
      display: flex;
      align-items: center;
      a{
        display: block;
        width: 100%;
        color: black;
        text-decoration: none;
        padding: 5px;
        align-items: center;
        @media (min-width:768px) {
          height: 29px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        &:hover,&:active{
          background: rgba(0,0,0,0.15);
        }
      }
      .loopactive{
        color:#2896F0;
      }
      button{
        outline: none;
        border: none;
        cursor: pointer;
        color: black;
        background: #fff;
        &:hover,&:active{
          color:#2896F0;
        }
      }
    }
}    
.actived{
  background: rgba(0,0,0,0.15);
}
.music-wrap h2{
    margin: 0 0 10px 0;
}
.upload{
    display: inline-block;
    position: relative;
    cursor: pointer;
    background-color: rgba(0,0,0,1);
    color: white;
    border-radius: 5px;
    padding: 5px;
    margin-right: 10px;
    transition: .3s;
    &:hover,&:active{
      background: rgba(0,0,0,0.6);
    }
}
.upload input[type='file']{
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
}
.progess{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    flex-grow: 100%;
    @media (max-width:768px) {
      display: none;
    }
}
.progess-bar{
    width: 200px;
    height: 5px;
    background: #2896F0;
    border-radius: 5px;
    position: relative;
    &::after{
      content: '';
      position: absolute;
      top: -2.5px;
      left: -3.5px;
      width: 210px;
      height: 10px;
      border-radius: 5px;
      background:black;
      z-index: -1;
  }
}

.layout{
    width: 210px;
    div{
        display: flex;
        justify-content: space-between;
    }
}

.noclick,.noclick:hover{
    background: rgba(0,0,0,0.2);
    cursor: not-allowed;
}
.fas{
  font-size: 24px;
}